import React, { createContext, useContext, useState, useEffect } from 'react';
import {
    createUserWithEmailAndPassword,
    signInWithEmailAndPassword,
    signOut,
    onAuthStateChanged,
    GoogleAuthProvider,
    signInWithPopup,
    signInWithRedirect,
    getRedirectResult,
    sendPasswordResetEmail,
    updateProfile
} from 'firebase/auth';
import { auth, isFirebaseConfigured } from '../firebase';
import axios from 'axios';

const AuthContext = createContext({});

export const useAuth = () => {
    return useContext(AuthContext);
};

// Helper function to save/update user in Django backend
const saveUserToDjango = async (firebaseUser) => {
    try {
        console.log(' Attempting to save user to Django...', {
            uid: firebaseUser.uid,
            email: firebaseUser.email,
            displayName: firebaseUser.displayName
        });

        const userData = {
            username: firebaseUser.uid, // Use Firebase UID as username
            email: firebaseUser.email,
            first_name: firebaseUser.displayName ? firebaseUser.displayName.split(' ')[0] : '',
            last_name: firebaseUser.displayName ? firebaseUser.displayName.split(' ').slice(1).join(' ') : ''
        };

        console.log(' Sending user data to /api/create-user/:', userData);

        const response = await axios.post('/api/create-user/', userData);

        console.log(' User saved to Django database:', response.data);
    } catch (error) {
        // Log detailed error information
        console.error(' Error saving user to Django:', {
            message: error.message,
            response: error.response?.data,
            status: error.response?.status,
            fullError: error
        });
    }
};

export const AuthProvider = ({ children }) => {
    const [currentUser, setCurrentUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [isStaff, setIsStaff] = useState(false);

    // Check if user is staff in Django backend
    const checkStaffStatus = async (user) => {
        if (!user) {
            setIsStaff(false);
            return;
        }

        try {
            const response = await axios.post('/api/products/check-staff/', {
                username: user.uid
            });

            setIsStaff(response.data.is_staff || false);
            console.log(' Staff status:', response.data.is_staff);
        } catch (error) {
            console.error('Error checking staff status:', error);
            setIsStaff(false);
        }
    };

    // Sign up with email and password
    const signup = async (email, password, displayName) => {
        try {
            setError(null);
            const userCredential = await createUserWithEmailAndPassword(auth, email, password);

            // Update profile with display name
            if (displayName) {
                await updateProfile(userCredential.user, { displayName });
            }

            // Save user to Django backend
            await saveUserToDjango(userCredential.user);

            return userCredential;
        } catch (err) {
            setError(err.message);
            throw err;
        }
    };

    // Login with email and password
    const login = async (email, password) => {
        try {
            setError(null);
            const userCredential = await signInWithEmailAndPassword(auth, email, password);

            // Sync user to Django database
            await saveUserToDjango(userCredential.user);

            return userCredential;
        } catch (err) {
            setError(err.message);
            throw err;
        }
    };

    // Login with Google
    const loginWithGoogle = async () => {
        try {
            setError(null);
            const provider = new GoogleAuthProvider();

            // Use redirect on production (more reliable), popup on localhost
            const isLocalhost = window.location.hostname === 'localhost' ||
                window.location.hostname === '127.0.0.1';

            if (isLocalhost) {
                // Use popup for local development
                const result = await signInWithPopup(auth, provider);
                await saveUserToDjango(result.user);
                return result;
            } else {
                // Use redirect for production (Render)
                await signInWithRedirect(auth, provider);
                // Note: redirect will reload the page, so saveUserToDjango 
                // will be called in the useEffect handling redirect result
                return null;
            }
        } catch (err) {
            setError(err.message);
            throw err;
        }
    };

    // Logout
    const logout = async () => {
        try {
            setError(null);
            await signOut(auth);
        } catch (err) {
            setError(err.message);
            throw err;
        }
    };

    // Reset password
    const resetPassword = async (email) => {
        try {
            setError(null);
            await sendPasswordResetEmail(auth, email);
        } catch (err) {
            setError(err.message);
            throw err;
        }
    };

    // Update user profile
    const updateUserProfile = async (updates) => {
        try {
            setError(null);
            if (currentUser) {
                await updateProfile(currentUser, updates);
            }
        } catch (err) {
            setError(err.message);
            throw err;
        }
    };

    // Listen for auth state changes and handle redirect results
    useEffect(() => {
        // Skip if auth is not initialized (Firebase not configured)
        if (!auth) {
            setLoading(false);
            return;
        }

        // Handle redirect result from Google sign-in
        getRedirectResult(auth)
            .then((result) => {
                if (result && result.user) {
                    console.log(' Redirect sign-in successful');
                    // Save user to Django after redirect
                    saveUserToDjango(result.user);
                }
            })
            .catch((error) => {
                console.error(' Redirect sign-in error:', error);
                setError(error.message);
            });

        const unsubscribe = onAuthStateChanged(auth, async (user) => {
            setCurrentUser(user);
            await checkStaffStatus(user);
            setLoading(false);
        });

        return unsubscribe;
    }, []);

    const value = {
        currentUser,
        signup,
        login,
        loginWithGoogle,
        logout,
        resetPassword,
        updateUserProfile,
        error,
        loading,
        isConfigured: isFirebaseConfigured,
        isStaff
    };

    return (
        <AuthContext.Provider value={value}>
            {!loading && children}
        </AuthContext.Provider>
    );
};
